import React ,{useEffect, useState} from 'react';
import 'antd/dist/antd.css';
import { Table, Typography, Row, Col, Switch, Button} from 'antd';
import { Link } from 'react-router-dom';
import { WechatOutlined, UserOutlined } from '@ant-design/icons';

const { Title, Text } = Typography;

export { Scoreboard };

function Scoreboard(props) {

    // console.log(props);
    const [scoreType, setScoreType] = useState({
        decimal: true
    });

    const [scoreFilter, setScoreTotal] = useState(null)

    function onChange(checked) {
        setScoreType({
            decimal: !scoreType.decimal
        })
    }

    const columns = [
        {
            title: 'Tn hc phn',
            dataIndex: 'subject_name',
            key: 'subject_name',
            width: 180,
        },
        {
            title: 'M hc phn',
            dataIndex: 'subject_code',
            key: 'subject_code',
            width: 110,
        },
        
        {
          title: 'Tn ch',
          dataIndex: 'credits_number',
          key: 'credits_number',
          width: 80,
        },
        {
            title: 'im h 10',
            dataIndex: 'score',
            key: 'score',
            width: 110,
        },
        {
            title: 'im h 4',
            dataIndex: 'scoref',
            key: 'scoref',
            width: 100,
        },
        {
            title: 'K hc',
            dataIndex: 'semester_name',
            key: 'semester_name',
            width: 180,
            filters: props.semesterList,
            onFilter: (value, record) => {
                console.log(value);
                return record.semester_name.includes(value);
            },
            filterSearch: true,
        },
    ]

    function configChatButton(){
        return (<Link to={`/chat/${props.scoreTotal.vnu_id}`}>
                    <Button type="primary" shape="round" icon={<WechatOutlined />} size="small" style={{marginBottom: "8px"}}> Nhn tin </Button>
                    </Link>);
    }
    return (
        <div>
            <Row>
            <Col flex="250px">
                <Title level={4}>{props.scoreTotal.name}</Title>
                <div>
                    <b>VNU ID:</b> {props.scoreTotal.vnu_id}
                    <br/>
                    <br/>
                    <b>CPA:</b> {props.scoreTotal.cpa}
                    <br/>
                    <b>S tn ch:</b> {props.scoreTotal.total_credits}
                    <br/>
                    <b>Trng thi:</b> {props.scoreTotal.stt}
                    <br/>
                    <br/>
                    <i>Nhn nt lc  ct <b>K hc</b> </i> 
                    <br/>
                    <i>xem chi tit tng k</i>
                    <br/>
                    {/* <Switch defaultChecked={scoreType.decimal} onChange={onChange} /> */}
                    <br/>
                    {props.isPersonal === false ? configChatButton() : ""}
                    <br/>
                    <Button type="primary" shape="round" size="small" onClick={onChange} > {scoreType.decimal === true? "Xem im h 4" : "Xem im h 10"} </Button>
                </div>
            </Col>
            <Col flex="auto">
                <Table
                columns={scoreType.decimal === true ? columns.filter(col => col.dataIndex !== 'scoref') : columns.filter(col => col.dataIndex !== 'score')}
                dataSource={props.scoreboard}
                summary={pageData => {
                    let summCredit = 0;
                    let summScore = 0;
            
                    pageData.forEach(({ credits_number, score, scoref }) => {
                        summCredit += credits_number;
                        summScore += score*credits_number;
                    });
            
                    return (
                      <>
                        <Table.Summary.Row>
                          <Table.Summary.Cell colSpan={2}><b>Tng kt</b></Table.Summary.Cell>
                          <Table.Summary.Cell>
                            <Text ><b>{summCredit}</b></Text>
                          </Table.Summary.Cell>
                          <Table.Summary.Cell>
                            <Text><b>{scoreType.decimal === true ? (summScore/summCredit).toFixed(2) : ((summScore/summCredit)/10*4).toFixed(2)}</b></Text>
                          </Table.Summary.Cell>
                          <Table.Summary.Cell/>
                          {/* <Table.Summary.Cell>
                            <Text>{totalRepayment}</Text>
                          </Table.Summary.Cell> */}
                        </Table.Summary.Row>
                        {/* <Table.Summary.Row>
                          <Table.Summary.Cell>Balance</Table.Summary.Cell>
                          <Table.Summary.Cell colSpan={2}>
                            <Text type="danger">{totalBorrow - totalRepayment}</Text>
                          </Table.Summary.Cell>
                        </Table.Summary.Row> */}
                      </>
                    );
                  }}
                bordered
                
                />
            </Col>
            </Row>
        </div>
    );
}