import React ,{useEffect, useState} from 'react';
import 'antd/dist/antd.css';
import { Form, Input, Button, Divider} from 'antd';

import {useClassActions, useStudentInfoAction} from '_actions';
import { UploadForm } from '_components/dbportal/UploadForm';
import { useClassWrapper } from '_helpers';

export {AddStudentForm} 
var upload_form = [
    {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
    },
    {
        title: 'Li',
        dataIndex: 'error',
        key: 'error',
    },
]
function AddStudentForm(props) {

    const [form] = Form.useForm();
    const studentInfoAction = useStudentInfoAction();
    const classWrapper = useClassWrapper();

    // useEffect(() =>{
    //     const curs = JSON.parse(localStorage.getItem('currentClass'));
    //     console.log(curs);
    //     if (curs!=null){
    //         setCurCl(curs.class_id);
    //     }
    //     console.log(curCl);
    // },[props]);

    function handleSubmit() {
        form.validateFields()
        .then((values) => {
            studentInfoAction.AddStudent(values.email);
        })
    };

    return (
        <div>
            <h6> Nhp email sinh vin </h6>
            <Form
                form = {form}
                layout="vertical"
                name="form_in_modal"
                initialValues={{
                    modifier: 'public',
                }}
            >
        <i>Thm nhiu sinh vin cch nhau bng du ','</i><br/>
        <i>Sinh vin c thm vo lp phi c ti khon VNU c cp bi admin ca h thng.</i><br/><br/>
          <Form.Item
            name="email"
            label="Email ca sinh vin: "
            rules={[
              {
                required: true,
                message: 'Ghi a ch email ca sinh vin cn thm  y',
              },
            ]}
            >
            <Input />
          </Form.Item>
          {/* <p>Thm nhiu sinh vin cch nhau bng du ',' </p>
          <p>Sinh vin c thm vo lp phi c ti khon VNU c cp bi admin ca h thng. </p> */}
          <Form.Item label=" " colon={false}>
                        <Button type="primary" htmlType="submit" onClick = {handleSubmit}>
                            Thm sinh vin
                        </Button>
                    </Form.Item>
        </Form>
        <Divider/>
        <h6> Ti file danh sch </h6>
        <i>Thm sinh vin vo lp bng file danh sch email.</i><br/>
        <i>Sinh vin c thm vo lp phi c ti khon VNU c cp bi admin ca h thng.</i><br/><br/>
        <UploadForm columns={upload_form} formurl={classWrapper.curClass != null ? "http://localhost:3000/api/classes/" + classWrapper.curClass.class_id + "/members/import/" : ""}/> 
        <br/><br/>
        </div>
        
    )
}